import React from 'react';
import profile from './img/profilecirc.png';

class Navbar extends React.Component {
    render() {
        const imgstyle = {
            width: '100px',
            height: 'auto',
            padding: '4px',
        }
        const navstyle = {
            backgroundColor: '#002549',
            borderBottom: '5px solid white',
            position: 'fixed',
            top: '0',
            width: '100%',
        }
        const toptext = {
            color: 'white',
            textWeight: "bold",
            textAlign: 'center',
            marginBottom: '-1px',
            marginTop: '-1px',
            textShadow: '2px 2px black',
        }


        return (
            <div className='row' style={navstyle}>
                <div className='col-3 col-s5'>
                    <img className='lg-float-right' src={profile} alt="" style={imgstyle}/>
                </div>
                 <div className='col-7 col-s7'>
                    <h1 className='sm-font-h1 xsm-font-h1 lg-text' style={toptext}> Kancelaria  Radcy Prawnego</h1>
                    <h1 className='sm-font-h1 xsm-font-h1' style={toptext}>Krystian Szulc</h1>
                 </div>
            </div>
            )
            }
            }

export default Navbar;